.footer {
    background-image: linear-gradient(to right, #f8cc9a 3%, #fd8a16 51%, #ff6b01 100%);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.footer__container {
    padding: 1rem 0 2rem;
}

.footer__title, .footer__link {
    color: var(--title-color);
}

.footer__title {
    text-align: center;
    margin-bottom: var(--mb-2);
}

.footer__link:hover {
    color: white;
}

.footer__list {
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
}

.footer__social {
    display: flex;
    justify-content: center;
    column-gap: 1.25rem;

}

.footer__social-link {
    background-color: var(--title-color);
    color: black;
    font-size: 1.25rem;
    padding: 04rem;
    border-radius: 0.5rem;
    display: inline-flex;
}

.footer__social-link:hover {
    background-color: var(--title-color-dark);
    color: white;
}

.footer__copy {
    display: block;
    margin-top: 4.5rem;
    color: var(--title-color);
    text-align: center;
    font-size: var(--smaller-font-size);
}

/* BREAK POINTS */
@media screen and (max-width: 992px) {
    .footer__social-link {
        padding: 0.25rem;
        border-radius: 0.25rem;
        font-size: 1rem;
    }
}